html,
body {
  height: 100%;
}
body {
  padding: 0 50px;
  margin: 0;
  font-family: 'Roboto', sans-serif;
  background-image: url(http://i.imgur.com/oavv3DC.jpg);
}
#wrap {
  min-height: 100%;
  width: 1200px;
  margin: 0 auto;
  padding: 20px 0;
  border-right: 1px solid #3B3B3B;
  background-color: #3B3B3B;
  overflow: hidden;
}
.container {
  padding-left: 17px;
  overflow: hidden;
}
.container .components-1 {
  overflow: hidden;
  margin-bottom: 25px;
}
.container .components-1 .business-card,
.container .components-1 .post-card {
  float: left;
  margin-left: 30px;
}
.container .components-1 .tabs-holder {
  float: left;
  margin-left: 30px;
  margin-top: -32px;
}
.container .search-fields {
  overflow: hidden;
}
.container .search-fields > div {
  float: left;
  margin-left: 30px;
}
.container .form-fields {
  float: left;
  position: relative;
  margin-left: 27px;
}
.container .form-fields .number-field.small {
  position: absolute;
  right: -20px;
  bottom: 40px;
}
.container .progress-bars {
  float: left;
  position: relative;
  margin: 30px 50px 50px;
}
.container .progress-bars > div {
  margin-bottom: 50px;
}
.container .progress-bars .p-bar-holder:nth-child(2) {
  margin-bottom: 90px;
}
.container .progress-bars .p-bar-holder:nth-child(2) .p-bar-loader {
  width: 70%;
}
.container .progress-bars .p-bar-holder:nth-child(3) .p-bar-loader,
.container .progress-bars .p-bar-holder:nth-child(6) .p-bar-loader,
.container .progress-bars .p-bar-holder:nth-child(10) .p-bar-loader {
  width: 50%;
}
.container .progress-bars .p-bar-holder:nth-child(4) .p-bar-loader,
.container .progress-bars .p-bar-holder:nth-child(7) .p-bar-loader {
  width: 60%;
}
.container .progress-bars .p-bar-holder:nth-child(5) .p-bar-loader,
.container .progress-bars .p-bar-holder:nth-child(8) .p-bar-loader {
  width: 80%;
}
.container .progress-bars .p-bar-holder:nth-child(11) .p-bar-loader {
  width: 100%;
}
.container .progress-bars .p-bar-holder:nth-child(12) .p-bar-loader {
  display: inline-block;
  width: 180px;
  left: 22%;
}
.container .progress-bars .tg-first {
  left: 22%;
}
.container .progress-bars .tg-second {
  left: 65%;
}
.container .buttons {
  margin-bottom: 130px;
}
.container .drop-downs {
  margin-left: 27px;
  float: left;
}
.container .drop-downs-scrolled {
  float: left;
  position: relative;
  margin: 0 50px;
}
.container .tags .tag-group {
  display: block;
  margin-bottom: 8px;
}
.container .tags .tag-long {
  margin-right: 150px;
  margin-bottom: 5px;
}
.tooltips,
.navigation,
.graphs {
  display: inline-block;
  vertical-align: top;
}
.navigation,
.graphs {
  width: 805px;
}
.navigation .simple-pagging-nav,
.graphs .simple-pagging-nav {
  margin-bottom: 150px;
}
.graphs {
  margin-bottom: 50px;
}
.tooltips {
  float: right;
  width: 320px;
  margin-right: 10px;
}
